/* Contexto.css */
.contexto-container {
  font-family: 'Poppins', sans-serif;
  background-color: #000;
  padding: 2rem;
  text-align: center;
  color: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  overflow-x: hidden; /* Prevent horizontal scroll */
  overflow-y: auto; /* Enable vertical scroll */
  height: 100vh;
  box-sizing: border-box; /* Include padding in height calculation */
  padding-bottom: 80px; /* Add extra padding at bottom */

}

/* Zenitsu Lightning Effects */
.zenitsu-lightning {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
  overflow: hidden; /* Prevent scrollbars on lightning */
  height: calc(100% - 80px); /* Prevent overlapping with fixed button */

}

.lightning-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at center, rgba(255,230,100,0.3) 0%, rgba(0,0,0,0) 70%);
  animation: flash 0.3s ease-out;
}


/* Make all interactive elements appear above lightning */
.contexto-input, .button-row, button, .guesses-container, 
.guess-box-wrapper, .popup-overlay, .popup-box {
  position: relative;
  z-index: 10;
}

/* Rest of your existing CSS stays the same */
.contexto-input {
  padding: 12px 16px;
  font-size: 16px;
  border-radius: 6px;
  border: 2px solid #ffcc00;
  width: 280px;
  margin-bottom: 12px;
  margin-right: 8px;
  background-color: rgba(0,0,0,0.7);
  color: white;
}

.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  backdrop-filter: blur(5px);
}

.popup-box {
  background-color: #222;
  padding: 2rem;
  border-radius: 10px;
  max-width: 80%;
  width: 400px;
  text-align: center;
  border: 2px solid #ffcc00;
  box-shadow: 0 0 20px rgba(255, 204, 0, 0.5);
  animation: popIn 0.3s ease-out forwards;
}

@keyframes popIn {
  from {
    transform: scale(0.8);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.popup-box h2 {
  color: #ffcc00;
  margin-top: 0;
}

.popup-box button {
  margin-top: 1rem;
  background-color: #ffcc00;
  color: #000;
  font-weight: bold;
  padding: 0.5rem 1.5rem;
}

.popup-box button:hover {
  background-color: #ffaa00;
}
.back-button {
  position: fixed; /* Change from default to fixed */
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 100; /* Higher than lightning background */
  background-color: #ffaa00;
  color: black;
  padding: 10px 20px;
  border-radius: 6px;
  border: none;
  font-weight: bold;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.back-button:hover {
  background-color: #ffcc00;
}
/* Enhanced button effects */
button {
  padding: 10px 18px;
  margin: 0 6px;
  font-size: 15px;
  border-radius: 6px;
  border: none;
  background-color: #ffaa00;
  color: black;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: bold;
  position: relative;
  overflow: hidden;
}

button:hover {
  background-color: #ffcc00;
  box-shadow: 0 0 15px #ffcc00;
}

button:active {
  transform: scale(0.98);
}

button::after {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(
    to bottom right,
    rgba(255,255,255,0) 45%,
    rgba(255,255,255,0.8) 50%,
    rgba(255,255,255,0) 55%
  );
  transform: rotate(30deg);
  opacity: 0;
}

button:hover::after {
  animation: button-shine 1.5s infinite;
}

@keyframes button-shine {
  0% {
    transform: translateX(-100%) rotate(30deg);
    opacity: 0;
  }
  20% {
    opacity: 1;
  }
  100% {
    transform: translateX(100%) rotate(30deg);
    opacity: 0;
  }
}

/* Add this to your Contexto.css file */

.guesses-container {
  width: 100%;
  max-width: 500px;
  margin: 20px auto;
}

.guess-box-wrapper {
  position: relative;
  height: 40px;
  margin-bottom: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.guess-box-filled {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  transition: width 0.5s ease;
}

.guess-box-content {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 12px;
  z-index: 2;
}

.guess-word {
  font-weight: bold;
  color: white;
  text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
}

.guess-rank {
  font-weight: bold;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 2px 8px;
  border-radius: 12px;
}
.content-wrapper {
  width: 100%;
  max-width: 800px;
  position: relative;
  z-index: 10;
  padding-bottom: 2rem; /* Extra space at bottom */
}

/* Prevent pull-to-refresh on mobile */
body {
  overscroll-behavior-y: contain;
}